.input {
  width: 360px;
  height: 40px;
  border: 2px solid #feb236;
  border-radius: 8px;
  padding-left: 6px;
  box-shadow: 5px 5px 15px -6px #034f84;
  font-size: 18px;
}

.input:focus {
  border: 2px solid #feb236;
  border-radius: 8px;
  outline: 0;
  box-shadow: 0px 0px 10px 6px rgba(227, 179, 64, 0.2) inset;
}

.form {
  width: 520px;
  display: flex;
  justify-content: space-between;
}

.submitButton {
  width: 140px;
  height: 46px;
  border-radius: 8px;
  background-color: transparent;
  border: 2px solid #feb236;
  color: #feb236;
  font-size: 18px;
  cursor: pointer;
  box-shadow: 5px 5px 15px -6px #034f84;
}

.submitButton:hover {
  border: 2px solid white;
  color: white;
  background-color: #feb236;
}

.submitButton:focus {
  outline: 0;
}
